 /* bring in normalize.css styles */
 @import 'normalize.css';
 body {
   
   margin: 0;
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
     'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
     sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
 }
 
 code {
   font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
     monospace;
 }
 h1,
 h2,
 h3,
 p,
 ul {
   margin: 0;
 }
 
 ul {
   padding: 0;
   list-style: none;
 }
 
 a {
   text-decoration: none;
   color: inherit;
 }
 * {
   box-sizing: border-box;
 }
 
 .container {
   max-width: 1240px;
   margin: 0 auto;
   padding: 15px 10px ;
 }
 .no-scroll {
   overflow: hidden;
 }
 h1 {
  text-align: center;
  margin-bottom: 15px;
  color: #9b2d30;
 }
 @media screen and (max-width: 765px)  {
 .header-link {
   display: none;
   box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
 }
 }
 @media screen and (min-width: 766px) and (max-width: 1027px) {
   .header-link {
     text-decoration: none;
     font-size: 15px;
     padding: 10px 6px;
     box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
     transition: all 0.3s;
     
     &:hover,
     &:focus {
     text-shadow: 3px 3px 20px #ff3cac, -2px 1px 30px #ff3cac;
      
     color: white;
   
   }
   }
   
 }
 @media screen and (min-width: 1028px)  {
   .header-link {
     text-decoration: none;
     font-size: 20px;
     padding: 10px 10px;
     box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
     transition: all 0.3s;
     
     &:hover,
     &:focus {
     text-shadow: 3px 3px 20px #ff3cac, -2px 1px 30px #ff3cac;
    
     color: white;
   
   }
   }
 }
 
  .header-link.active {
   
   background-color:  #9b2d30;
   color: white;
 }
 body {
   background-color: white;
 }
 body.modal-open {
   overflow: hidden; 
 }
 .title {
   text-align: center; 
   margin-bottom: 30px;
   font-family: "Trirong", serif;
   font-weight: 300;
   font-style: normal;
 font-size: 40px;
 color: blueviolet;
 }
 